import React, {useState} from 'react'
import { Form, Input, Button, Checkbox, Row, Col, Select, Upload, message } from 'antd';
import { UploadOutlined } from '@ant-design/icons';
import _ from 'lodash'
import {User, VisaStatus} from '../../model/model'
import FormProps from './FormProps'
import {UploadFile} from 'antd/lib/upload/interface'
import {storage_ref} from '../../lib/firebase'
const {Option} = Select

export const Form2: React.FC<FormProps> = (props) => {
    const {currentUser, changeCurrentUser, changeStep} = props
    const [fileList, updateFileList] = useState<UploadFile[]>([])
    const [form] = Form.useForm();

    const goBack = () => {
        changeStep(-1)
    }

    const onFinish = (values) => {
        const newUser = {
            email: currentUser.email,
            firebaseId: currentUser.firebaseId,
            firstName: currentUser.firstName,
            lastName: currentUser.lastName,
            phoneNumber: currentUser.phoneNumber,
            location: currentUser.location,
            salary: currentUser.salary,
            portfolio: currentUser.portfolio,
            visa: currentUser.visa,
            resume: currentUser.resume,
            linkedin: values.linkedin,
            dribbble: values.dribbble,
        }
        changeCurrentUser(newUser)
        changeStep(1)
    }

    const onFinishFailed = () => {
        message.error('There was an error completing your account. Ensure every field is filled out.')
    }

    return (
        <Form
            name="basic"
            initialValues={{ remember: true }}
            onFinish={onFinish}
            onFinishFailed={onFinishFailed}
        >
            <Form.Item
                label="LinkedIn"
                name="linkedin"
            >
                <Input placeholder="https://www.linkedin.com/in/username"/>
            </Form.Item>

            <Form.Item
                label="Dribbble"
                name="dribbble"
            >
                <Input placeholder="https://dribbble.com/username"/>
            </Form.Item>

            <Form.Item>
                <Row gutter={12} justify="end" align="middle">
                    <Button type="default" onClick={goBack}>
                        Go Back
                    </Button>
                </Row>
                <Row gutter={12} justify="end" align="middle">
                    <Button type="primary" htmlType="submit">
                        Take the Quiz
                    </Button>
                </Row>
            </Form.Item>
        </Form>
    );
}